.gxInput {
  min-height: 4.5rem;
  font-size: 1.125rem;
}
input,
input::-webkit-input-placeholder {
  font-size: 1.125rem;
}
.gxAssistive {
  font-size: 0.75rem;
}
.loading::after {
  position: absolute;
  bottom: 0;
  content: '';
  border-bottom: 1px solid var(--primary);
  animation: loading 1.3s ease-in-out infinite;
}

@keyframes loading {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 100%;
    transform: translate(100%);
  }
}
label {
  --dashArray: 0;
  --dashOffset: 0;
}
.loadingDarkPath {
  stroke-dasharray: var(--dashArray);
  animation: dash 5s linear infinite;
}

@keyframes dash {
  to {
    stroke-dashoffset: var(--dashOffset);
  }
}

.inputContainer {
  height: 50px;
}
input {
  letter-spacing: 1px;
}
span {
  position: absolute;
  top: 1.125rem;
  box-sizing: border-box;
  line-height: 1rem;
  transition: all 0.2s ease 0s;
}
.gx-left-8 {
  left: 2rem;
}
input:focus + span:not(.label-hidden),
.active-label span:not(.label-hidden) {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0.75rem;
  line-height: 12px;
}
span.label-hidden,
span.label-hidden {
  font-size: 0.75rem;
  transform: translate(0, 1rem);
}
input:-moz-ui-invalid {
  border: none;
  outline: none;
  box-shadow: 0 0 0;
}

// input:-webkit-autofill + span,
// input:focus:-webkit-autofill + span {
//   font-size: 0.75rem;
//   transform: translate(0, -0.8rem);
// }
input:-webkit-autofill {
  top: 1.25rem;
  bottom: 1rem;
  font-size: 1rem;
  color: var(--black);
  background-color: transparent;
  border: none;
  -webkit-box-shadow: 0 0 0 1000px var(--white) inset;
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: var(--black);
}
input.dark:-webkit-autofill {
  color: white;
  -webkit-box-shadow: 0 0 0 1000px var(--secondary-200) inset;
  -webkit-text-fill-color: white;
}
// input.dark:-webkit-autofill,
// input.dark:-webkit-autofill:hover,
// input.dark:-webkit-autofill:focus,
// input.dark:-webkit-autofill:active  {
//     -webkit-box-shadow: 0 0 0 30px red inset !important;
// }

//Firefox invalid state for inputs such as email
input:invalid,
input:-moz-ui-invalid {
  border: none;
  outline: none;
  box-shadow: 0 0 0 1000px var(--secondary-200) inset;
}
.nunitoFamily {
  font-family: 'Nunito';
}
input[type='file'] {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
}
.file-name {
  position: absolute;
  top: 1.5rem;
  left: 0;
  box-sizing: border-box;
  overflow: hidden;
  line-height: 1rem;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media screen and (min-width: 768px) {
  .gxInput {
    min-height: 4.125rem;
    font-size: 0.75rem;
  }
  input,
  input::-webkit-input-placeholder {
    font-size: 0.875rem;
  }
  input.dark,
  input.dark::-webkit-input-placeholder {
    color: var(--white);
  }
  input:focus + span:not(.label-hidden),
  .active-label span:not(.label-hidden) {
    font-size: 0.625rem;
  }
  .gxAssistive {
    font-size: 0.625rem;
  }
}
